@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;700&display=swap);
.navbar {

    display: flex;

    height: 4rem;

    align-items: center;

    border-bottom-width: 2px;

    --tw-border-opacity: 1;

    border-color: rgba(244, 244, 245, var(--tw-border-opacity));

    --tw-text-opacity: 1;

    color: rgba(236, 72, 159, var(--tw-text-opacity))
}

.navbar__links {

    margin-left: auto;

    display: flex;

    align-items: center
}

.navbar__links > :not([hidden]) ~ :not([hidden]) {

    --tw-space-x-reverse: 0;

    margin-right: calc(1rem * var(--tw-space-x-reverse));

    margin-left: calc(1rem * calc(1 - var(--tw-space-x-reverse)))
}

.navbar__links {

    align-self: stretch
}

.navbar__links__item {

    display: flex;

    height: 100%;

    cursor: pointer;

    align-items: center;

    --tw-bg-opacity: 1;

    background-color: rgba(248, 250, 252, var(--tw-bg-opacity))
}

.navbar__links__item:hover {

    --tw-bg-opacity: 1;

    background-color: rgba(244, 244, 245, var(--tw-bg-opacity))
}

.figure {

    margin-bottom: 2rem;

    cursor: pointer;

    padding: 1rem;

    --tw-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);

    box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow)
}

.figure:hover {

    --tw-bg-opacity: 1;

    background-color: rgba(244, 244, 245, var(--tw-bg-opacity))
}

@media (min-width: 768px) {

    .figure {

        margin-bottom: 0px
    }

    .figure {

        --tw-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);

        box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow)
    }
}

.figure__title {

    font-size: 1.5rem;

    line-height: 2rem;

    text-transform: capitalize
}

.figure__date {

    font-size: 0.875rem;

    line-height: 1.25rem
}

.figure__author {

    margin-left: 0.5rem;

    margin-bottom: 1rem;

    display: block;

    font-size: 0.875rem;

    line-height: 1.25rem;

    --tw-text-opacity: 1;

    color: rgba(236, 72, 159, var(--tw-text-opacity))
}

.primaryHeading {
    margin-top: 1rem;
    margin-bottom: 1rem;
    text-align: center;
    font-size: 2.25rem;
    line-height: 2.5rem;
    font-weight: 700
}

