.container{
	width: 100%;
	margin-right: auto;
	margin-left: auto;
}
@media (min-width: 640px){

	.container{
		max-width: 640px;
	}
}
@media (min-width: 768px){

	.container{
		max-width: 768px;
	}
}
@media (min-width: 1024px){

	.container{
		max-width: 1024px;
	}
}
@media (min-width: 1280px){

	.container{
		max-width: 1280px;
	}
}
@media (min-width: 1536px){

	.container{
		max-width: 1536px;
	}
}
.flex{
	display: flex;
}
.grid{
	display: grid;
}
.h-screen{
	height: 100vh;
}
.grid-cols-1{
	grid-template-columns: repeat(1, minmax(0, 1fr));
}
.items-center{
	align-items: center;
}
.justify-center{
	justify-content: center;
}
.justify-between{
	justify-content: space-between;
}
.gap-4{
	gap: 1rem;
}
.bg-pink-light{
	--tw-bg-opacity: 1;
	background-color: rgba(236, 72, 159, var(--tw-bg-opacity));
}
.p-3{
	padding: 0.75rem;
}
.py-4{
	padding-top: 1rem;
	padding-bottom: 1rem;
}
.text-2xl{
	font-size: 1.5rem;
	line-height: 2rem;
}
.text-4xl{
	font-size: 2.25rem;
	line-height: 2.5rem;
}
.text-6xl{
	font-size: 3.75rem;
	line-height: 1;
}
.font-extrabold{
	font-weight: 800;
}
.text-white-light{
	--tw-text-opacity: 1;
	color: rgba(248, 250, 252, var(--tw-text-opacity));
}
.filter{
	filter: var(--tw-filter);
}

body {
  font-family: 'Poppins', sans-serif;
  color: #36454f;
}

.hover\:bg-pink-dark:hover{
	--tw-bg-opacity: 1;
	background-color: rgba(219, 39, 119, var(--tw-bg-opacity));
}

@media (min-width: 768px){

	.md\:grid-cols-2{
		grid-template-columns: repeat(2, minmax(0, 1fr));
	}
}

@media (min-width: 1536px){

	.\32xl\:grid-cols-3{
		grid-template-columns: repeat(3, minmax(0, 1fr));
	}
}

